@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Quicksand";
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.content {
  padding-top: 80px;
}
.main {
    width: 90%;
    margin-left: 5%;
    padding: 20px;
}

.news {
    width: 80%;
    height: 400px;
    margin-left: 10%;
    margin-bottom: 100px;
    display: flex;
    flex-direction: row;
}

.news:hover {
    -webkit-text-decoration: underline 5px black;
            text-decoration: underline 5px black;
}

a {
    text-decoration: none !important;
}

.news-info {
    width: 40%;
    height: 100%;
    background-color: rgb(197, 4, 4);
    color: white;
    font-size: larger;
}

.news-info h3{
    color: black;
}

.news-image {
    height: 100%;
    width: 100%;
}

.news-image img {
    height: 100%;
    width: 100%;
}

.news-feed {
    width: 80%;
    margin-left: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 100px;
    color: black;
}

.news-feed img {
    width: 100%;
    height: 100%;
}

.news1, .news2, .news3 {
    height: 200px;
    width: 32%;
}

.news1 a, .news2 a, .news3 a {
    text-decoration: none;
    color: black;
}

.news1:hover, .news2:hover, .news3:hover {
    text-decoration: underline;
    color: black;
}

.videos {
    width: 80%;
    margin-left: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 100px;
}

.video1, .video2 {
    height: 250px;
    width: 48%;
}
.team-main {
    width: 80%;
    margin-left: 15%;
}
.team {
    text-align: left;
}

.team-title {
    font-size: 70px;
}

.players {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.player {
    margin-right: 50px;
    height: 415px;
    width: 310px;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.player-pic {
    height: 80%;
    width: 100%;
}

.player-pic img {
    height: 100%;
    width: 100%;
}

.player-num {
    height: 100%;
    width: 25%;
    font-size: 60px;
    border-right: 2px solid rgb(197, 4, 4);
}

.player-name {
    height: 100%;
    width: 75%;
    font-size: 30px;
}

.player-info {
    height: 20%;
    width: 99%;
    display: flex;
    flex-direction: row;
    line-height: 70px;
    text-align: center;
    border: 2px solid rgb(197, 4, 4);
}

.attackers, .midfielders, .defenders, .goalkeepers {
    margin-bottom: 100px;
}

.icon {
    width: 50px;
}
.main-fixtures {
    margin-top: 100px;
    width: 50%;
    margin-left: 25%;
    text-align: left;
}

.match {
    height: 120px;
    width: 100%;
    margin-bottom: 20px;
    border: 2px solid rgb(197, 4, 4);
    display: flex;
    flex-direction: row;

}

.league-cup, .premier-league {
    margin-bottom: 100px;
}

.teams {
    height: 100%;
    width: 45%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.match-info {
    height: 100%;
    width: 40%;
}

.stadium {
    height: 100%;
    width: 15%;
}

.team-pic {
    width: 110px;
}

.vs-tag {
    font-size: 60px;
    line-height: 120px;
}

.match-info {
    font-size: 20px;
    text-align: center;
}
.main-tickets {
    width: 50%;
    margin-left: 25%;
    background-color: rgba(220, 220, 220, 0.5);
}

.ticket-info {
    width: 100%;
    text-align: left;
    margin-left: 50px;
    
}

.credit-info {
    margin-left: 3rem;
}

.match-info {
    width: 100%;
    display: flex;
    flex-direction: row;
}

.info-icon {
    width: 40px;
    margin-left: 50px;
}

.info-icon:hover {
    cursor: pointer;
}

.card-info {
    width: 15rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#debit {
    width: 14rem;
    height: 2.5rem;
    font-size: 20px;
    border: 1px solid;
    border-radius: 3px;
}

#dateInputCredit {
    height: 2.5rem;
    width: 6rem;
    font-size: 20px;
    border: 1px solid;
    border-radius: 3px;
}

#code {
    height: 2.5rem;
    width: 3rem;
    font-size: 20px;
    border: 1px solid;
    border-radius: 3px;
}

#code:hover, #debit:hover, #dateInputCredit:hover {
    border: 2px solid black;
    background-color: rgba(197, 4, 4, 0.11);
    color: black;
}

#code:focus, #debit:focus, #dateInputCredit:focus {
    border: 2px solid black;
    background-color: rgba(197, 4, 4, 0.11);
    color: black;
}

/* Button style */
.submit-info {
    width: 150px;
    height: 40px;
    font-size: large;
    border: 2px solid rgb(197, 4, 4) !important;
    background-color: black !important;
    color: aliceblue !important;
    margin-left: 2rem;
}

.submit-info:hover {
    background-color: rgb(70, 69, 69) !important;
    color: aliceblue !important;
}

.submit-info:focus {
    background-color: black !important;
    color: aliceblue !important;
}

/* Contact styles */
.input-style {
    width: 50%;
    height: 40px;
    border-radius: 3px;
    border: 2px solid black;
    color: aliceblue;
    background-color: rgb(197, 4, 4);
    margin-left: 2rem;
}

.input-style:hover {
    cursor: pointer;
    background-color: rgb(104, 1, 1);
}

.input-style::-webkit-input-placeholder {
    color: aliceblue;
}

.input-style:-ms-input-placeholder {
    color: aliceblue;
}

.input-style::placeholder {
    color: aliceblue;
}

/* Tooltip info */
.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
  }
  
  .tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
  
    /* Position the tooltip */
    position: absolute;
    z-index: 1;
  }
  
  .tooltip:hover .tooltiptext {
    visibility: visible;
  }
.main-stats {
    width: 50%;
    margin-left: 25%;
    text-align: left;
}

table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
td, th {
    border: 1px solid black !important; 
    text-align: center;
    padding: 8px;
  }

.table-elem {
    text-align: center;
}

td:nth-child(1) {
    text-align: left;
  }

tr:nth-child(1) {
    background-color: rgb(197, 4, 4);
    color: aliceblue;
  }

  tr:nth-child(3) {
    background-color: rgba(197, 4, 4, 0.39);
  }
.main-contact {
    width: 50%;
    margin-left: 25%;
}

.quick-contact {
    text-align: justify;
    margin-bottom: 50px;
}

/* Contact styles */
.quick-contact > form > input {
    width: 50%;
    height: 40px;
    border-radius: 3px;
    border: 2px solid black;
    color: aliceblue;
    background-color: rgb(197, 4, 4);
}

.quick-contact > form > textarea {
    width: 50%;
    border-radius: 3px;
    border: 2px solid black;
    color: aliceblue;
    background-color: rgb(197, 4, 4);
}

.quick-contact > form > input::-webkit-input-placeholder {
    color: aliceblue;
}

.quick-contact > form > input:-ms-input-placeholder {
    color: aliceblue;
}

.quick-contact > form > input::placeholder {
    color: aliceblue;
}

.quick-contact > form > textarea::-webkit-input-placeholder {
    color: aliceblue;
}

.quick-contact > form > textarea:-ms-input-placeholder {
    color: aliceblue;
}

.quick-contact > form > textarea::placeholder {
    color: aliceblue;
}

.send-email {
    width: 150px;
    height: 40px;
    font-size: large;
    border: 2px solid rgb(197, 4, 4) !important;
    background-color: black !important;
    color: aliceblue !important;
}

.send-email:hover {
    background-color: rgb(70, 69, 69) !important;
    color: aliceblue !important;
}

.send-email:focus {
    background-color: black !important;
    color: aliceblue !important;
}
.nav-container {
    width: 100%;
    height: 80px;
    position: fixed;
    z-index: 1;
    background-color: rgb(197, 4, 4);
    display: flex;
    font-size: 25px;
    align-items: baseline;
    box-shadow: 0 5px 8px 0 rgb(0 0 0 / 10%), 0 3px 10px 0 rgb(0 0 0 / 10%) !important;
}

.navbar {
    padding: 20px;
    display: flex;
    align-items: center;
    max-width: 1100px;
    margin: 0 auto;
  }

  .navbar .links {
    margin-left: auto;
    max-width: 600px;
  }
  .navbar a {
    margin-left: 25px;
    text-decoration: none;
    padding: 6px;
    color: #f2f2f2;
  }

  .navbar a:hover {
    background-color: #f2f2f2;
    border-radius: 7px;
    color: rgb(197, 4, 4);
  }

  .navbar img {
    height: 70px;
    width: 70px;
  }
.news-main {
    width: 80%;
    margin-left: 10%;
}

.team-image img{
    width: 70%;
}

.news-main h1 {
    font-size: 60px;
}

.news-main p {
    font-size: 20px;
    text-align: justify;
}

.news-text {
    width: 70%;
    margin-left: 15%;
}
